.main {
  background-color: #fff;
  height: auto;
  

  @include on-tablet {
    height: 400px;
  }

  @include on-desktop {
    height: 460px;
  }
}

.main__content {
  display: flex;
  flex-direction: column;
  row-gap: 96px;
  padding-block: 96px;

  @include on-tablet {
    padding-block: 120px;
    row-gap: 120px;
  }
}
