.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include on-tablet {
      row-gap: 64px;
    }
  }
}

.category {
  &__photos {
    @include page-grid;

    margin-bottom: 16px;
  }

  &__link {
    &--left {
      height: 0;
      padding-bottom: 100%;
      position: relative;
      transition: transform 1s ease;

      &:hover {
        transform: scale(1.08);
      }

      @include on-tablet {
        grid-column: span 3;
      }

      @include on-desktop {
        grid-column: span 6;
      }
    }

    &--right {
      height: 0;
      padding-bottom: 100%;
      position: relative;
      transition: transform 1s ease;

      &:hover {
        transform: scale(1.08);
      }

      @include on-tablet {
        grid-column: 4/-1;
      }

      @include on-desktop {
        grid-column: 7/-1;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: url(/src/images/mice.svg), auto;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }

  &__button {
    display: flex;
    box-sizing: border-box;
    height: 56px;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: #f98921;
    border-radius: 8px;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    color: #fff;
    text-decoration: none;
    margin-top: 32px;
    cursor: url(/src/images/mice.svg), auto;


    @include on-desktop {
      margin-top: 56px;
      width: 324px;
    }

    &:hover {
      background-color: #1b2129;
    }
  }
}
