.recommended {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1/-1;

  }

  @include on-tablet {
    grid-column: 2/-2;
  }

  @include on-desktop {
    .recommended__product:nth-child(1) {
      grid-column: 1 / 5;
    }

    .recommended__product:nth-child(2) {
      grid-column: 5 / 9;
    }

    .recommended__product:nth-child(3) {
      grid-column: 9 / 13;
    }
  }
}

.product__title {
  font-weight: 700;
  font-size: 18px;
  letter-spacing: normal;
  line-height: 25.2px;
}

.product__price {
  font-weight: 700;
  letter-spacing: normal;
}

.product__photo {
  width: 100%;
  object-fit: cover;
  transition: transform 1s ease;
  cursor: url(/src/images/mice.svg), auto;

  &:hover {
    transform: scale(1.1);
  }
}
