.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    margin-bottom: 32px;

    position: sticky; /* to keep cross always visible */
    top: 0;
    z-index: 1;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    cursor: url(/src/images/mice.svg), auto;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    cursor: url(/src/images/mice.svg), auto;
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}
