.contacts__block {
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
  letter-spacing: 0.15px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;

  @include page-grid;
}

.form {
  box-sizing: border-box;
  margin-bottom: 48px;
  font-size: 14px;
  line-height: 19.6px;
  letter-spacing: 0.15px;

  border: 0;
  grid-column: 1/-1;

  @include on-desktop {
    grid-column: 1/7;
  }
}

.form__input {
  box-sizing: border-box;
  width: 100%;
  border-radius: 8px;
  outline: none;
  background-color: #f9f9f9;
  padding: 14px 16px;
  border: 0;
  height: 48px;
  margin-bottom: 16px;

  &::placeholder {
    color: #7e7e83;
  }

  &:hover {
    border: 1px solid #dbdbdb;
    color: $main-text-color;
  }

  &:focus {
    border: 2px solid #f98921;
    color: $main-text-color;
  }
}

.form__message {
  box-sizing: border-box;
  font-family: Manrope, sans-serif;
  height: 148px;
  width: 100%;
  border: 0;
  border-radius: 8px;
  outline: none;
  background-color: #f9f9f9;
  resize: none;
  padding: 14px 16px;

  &::placeholder {
    color: #7e7e83;
  }

  &:hover {
    border: 1px solid #dbdbdb;
    color: $main-text-color;
  }

  &:focus {
    border: 2px solid #f98921;
    color: $main-text-color;
  }
}

.form__button {
  display: flex;
  box-sizing: border-box;
  height: 56px;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #f98921;
  border: 0;
  outline: none;
  border-radius: 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  color: #fff;
  text-decoration: none;
  margin-top: 32px;
  margin-bottom: 48px;
  cursor: url(/src/images/mice.svg), auto;

  @include on-tablet {
    margin-bottom: 80px;
  }

  &:hover {
    background-color: #1b2129;
  }
}

.contacts__us {
  display: grid;
  box-sizing: border-box;
  border: 0;
  gap: 24px;
  text-align: left;
  font-size: 14px;
  line-height: 19.6px;
  height: 244px;

  @include on-tablet {
    grid-column: 1/5;
  }

  @include on-desktop {
    grid-column: 8/-1;
  }
}

.contacts__cell {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.small-text {
  color: #9393a3;
  padding: 1px 0;
}

.big-text {
  color: #191919;
  font-size: 16px;
  line-height: 22.4px;
  padding: 1px 0;

  &:hover {
    color: #f98921;
  }
}

.adress {
  text-decoration: none;
  color: $main-text-color;
  font-size: 16px;
  line-height: 22.4px;
  padding: 1px 0;

  &:hover {
    color: #f98921;
  }
}
