.about-us {
  background-color: #fcfaef;
  width: 100%;

  &__content {
    display: flex;

    flex-direction: column;

    @include contetn-padding-inline;

    padding-block: 96px;

    @include on-tablet {
      padding-block: 120px;
    }

    @include on-desktop {
      padding-block: 120px;
    }

    @include page-grid;
  }

  &__title {
    text-align: left;
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 1/4;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }

  &__description {
    grid-column: 1/-1;
    line-height: 22.4px;
    font-size: 16px;
    text-align: left;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }
}

.image-logic {
  height: 200px;
  width: 100%;
  background-repeat: no-repeat;
  object-position: center;
  object-fit: cover;

  @include on-tablet {
    height: 400px;
  }

  @include on-desktop {
    height: 460px;
  }
}
